.modal{
    position: fixed;
    top: 0;
    right:0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.5);
}
.modal_content {
    background-color: white;
    max-width: 496px;
    width: 100%;
    border-radius: 20px;
    padding: 34px 36px ;

}